import React,{PureComponent} from 'react';

import Cell from "../components/cell";
import {NavLink,Route,Switch,Redirect} from 'react-router-dom';
import Header from '../layouts/header.js'
import styles from '../assets/css/goods.module.css'
import Column from "./Trip";
import Follow from "./Follow";
import Detail from "./Detail";
import UcList from "../components/uc-list";

import {connect} from "react-redux";

import {clear,updateList} from "../store/actionCreators";

import { PullToRefresh } from 'antd-mobile';

class Goods extends PureComponent{
  constructor(props) {
    super(props);
    this.state = {
      refreshing: false,
      down: true,
      height: document.documentElement.clientHeight,
      data: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(
        updateList({collectionName:'follow',params:{_page:1,_limit:12},type:'UPDATE_GOODS'})
    )
  }
  render(){
    let {column}=this.props;

    return (
		
      <div className="pt">
        <PullToRefresh
            damping={60}
            ref={el => this.ptr = el}
            style={{
              height: this.state.height,
              overflow: 'auto',
            }}
            indicator={this.state.down ? {} : { deactivate: '上拉可以刷新' }}
            direction={this.state.down ? 'down' : 'up'}
            refreshing={this.state.refreshing}
            onRefresh={() => {
              this.setState({ refreshing: true });
              setTimeout(() => {
                this.setState({ refreshing: false });
              }, 1000);
            }}
        >
          {this.state.data.map(i => (
              <div key={i} style={{ textAlign: 'center', padding: 20 }}>
                {this.state.down ? 'pull down' : 'pull up'} {i}
              </div>
          ))}

        {
          column.map((item,index)=>(
              <UcList
                  key={item._id}
                  index={index}
                  data={item}
                  style={{height:'3rem'}}
                  to={{pathname:'/detail',apiname:'home'}}

              />
          ))
        }
        </PullToRefresh>
      </div>
    )
  }
}

export default connect(
    state=>({column:state.goods})
)(Goods)