import React,{PureComponent} from 'react';
import UcList2 from "../components/uc-list2";

import {connect} from "react-redux";

import {clear,updateList} from "../store/actionCreators";


import { NoticeBar, WhiteSpace, Icon } from 'antd-mobile';
class trip extends PureComponent{
	// state={
	//   column : []
	// };
	//
	// async componentDidMount(){
	//   let res =  await React.axios({url:'/api/goods/follow',params:{_page:1,_limit:10}})
	//   this.setState({column:res.data.data})
	// }

	componentDidMount() {
		this.props.dispatch(
			updateList({collectionName:'follow',params:{_page:1,_limit:10},type:'UPDATE_TRIP'})
		)
	}


	render(){
	  let {column}=this.props;
	  // console.log(this.props.match.url, this.props.match.path)
    return (
      <>
		  <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
			  Notice: The arrival time of incomes and transfers of Yu &#39;E Bao will be delayed during National Day.
		  </NoticeBar>
		  {
			  column.map((item,index)=>(
				  <UcList2
					  key={item._id}
					  index={index}
					  data={item}
					  style={{background:''}}
					  to={{pathname:'/detail',apiname:'home'}}

				  />
			  ))
		  }


      </>
    )
  }
}

export default connect(
	state=>({column:state.trip})
)(trip)