import React,{Component} from 'react';

import Cell from "../components/cell";
import UcButton from "../components/uc-button";
import UcList from "../components/uc-list";

import {connect} from "react-redux";
import {clear,updateList} from "../store/actionCreators";
import {List, PullToRefresh} from 'antd-mobile'
class Follow extends Component{
	state={
		refreshing:false
	}
	componentDidMount(){
		this.getData()
	}
	async getData(){
		this.props.dispatch(clear('CLEAR_FOLLOW'));
		this.props.dispatch(
			updateList({collectionName:'follow',params:{_page:1,_limit:12},type:'UPDATE_FOLLOW'})
		)

		this.setState({ refreshing: false });
	}



  render(){
	  let {follow}=this.props;

    return (
      <div className="pt">
		  {/*PullToRefresh 拉动刷新*/}
		  <PullToRefresh
			  refreshing={this.state.refreshing}
			  onRefresh={() => {
				  this.setState({ refreshing: true });
				  this.getData()
			  }}
		  >
        {
			follow.map(item=>(
            <Cell
              key={item._id}
              data={item}
              to={{pathname:'/detail',apiname:'follow'}}
            >
			<UcButton style={{float:'right'}} size="mini" clickHandler={this.show}>+</UcButton>
			</Cell>
          ))
        }
		  </PullToRefresh>
      </div>
    )
  }
}

export default connect(
	state=>({follow:state.follow})
)(Follow)